.home {

    background-color: rgba(171, 214, 249, 0.327);
    width: 100vw;


.home-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 80%;
    max-width: 1000px;
    margin: 0 auto;
    

    h1 {
        background: 
        linear-gradient(156deg, rgb(173, 215, 255) 0%, rgb(100, 180, 255) 4%, rgb(43, 97, 245) 61%, rgb(174, 0, 255) 100%);
       background-clip: border-box;
       -webkit-background-clip: text;
       -webkit-text-fill-color: transparent;
       text-align: center;
       font-size: 3rem;
       font-weight: 900;
       margin-top: 30px;
    }
    h2 {
        padding-bottom: 30px;
    }
}
}